
import { ref } from 'vue';
import { getTime, getDate } from '../utils';
import useStore from '../services/store';
import SettingsButtons from './SettingsButtons.vue';

export default {
  components: {
    SettingsButtons
  },
  setup() {
    const store = useStore();
    const time = ref('');
    const date = ref('');
   
    setInterval( () => {
      time.value = getTime();
      date.value = getDate();
    }, 1000);
    return { 
      store,
      time,
      date,
    };
  }
}
